.row1 {
    width: 100%;
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
  } 


.home1{
    display: flex;
    flex-flow: column wrap;
    align-items: center;

}

.home1 p{
    margin: 10px 0;
    text-align: justify;
}

.home2{
    width: 100%;
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;   
}

.row1 h1{
    color: #000;
    font-size: 100px;
    line-height: 15px;
    padding: 100px;

}

.home1 button{
    border: none;
    outline: none;
    background: #333;
    color: white;
    width: 100%;
    height: 40px;
    display: block;
    cursor: pointer;
    text-transform: uppercase;
    letter-spacing: 2px;
    margin: 15px 0;
    padding: 10px;
  
}

.card1{
    min-width: 300px;
    min-height: 400px;
    border: 1px solid #eee;
    overflow: hidden;
    padding: 10px;
    box-shadow: 2px 8px 20px #ddd;
    margin: 10px;
    background-image: url(../svg/pic-1.png);
    background-position: center;
    background-size: cover;
    transition: transform 0.5s;
}

.card2{
    background-image: url(../svg/pic-2.png);
    background-position: center;
}

.card3{
    background-image: url(../svg/pic-3.png);
    background-position: center;
}

.card4{
    background-image: url(../svg/pic-4.png);
    background-position: center;
}

.card1:hover{
    transform: translateY(-10px);
}

.card1 h5{
    color: #fff;
    text-shadow: 0 0 5px #999;
}

.card1 p{
    text-shadow: 0 0 5px #000;
}

